import { browser_api } from '@app/browser_api';
import { Actions } from '@app/actions/shared';
import { type OpenPopupRootMessage } from '@app/messages/shared';

window.addEventListener('load', async () => {
  try {
    requestIdleCallback(() => {
      const openPopupLink = document.querySelector('#openExtension');

      if (openPopupLink instanceof HTMLElement) {
        openPopupLink?.addEventListener('click', handleClickOpenPopup);
      }
    });
  } catch (error) {
    console.error('error', error);
  }
});

async function handleClickOpenPopup(event: MouseEvent): Promise<void> {
  try {
    event.preventDefault();
    event.stopPropagation();

    await browser_api.runtime.sendMessage<OpenPopupRootMessage>({
      action: Actions.OpenPopupRoot,
    });
  } catch (error) {
    console.error(error);
  }
}
